import React, { Component } from 'react'
import './Help.css'

export default class Help extends Component {
  render() {
    return (
      <div className='modal'>
        <h2>How to play?</h2>
        <hr />
        In this puzzle you need to a guess a 6 character word in 6 attempts.<br /><br />
        After each attempt, you will get feedback if the characters are present in the guessed word. <br />&nbsp;
        <table><tbody>
          <tr><td><div class="cell miss" style={{ height: 44, width: 44, fontSize: 36 }}>C</div></td><td><div class="cell miss" style={{ height: 44, width: 44, fontSize: 36 }}>H</div></td><td><div class="cell partial" style={{ height: 44, width: 44, fontSize: 36 }}>A</div></td><td><div class="cell correct" style={{ height: 44, width: 44, fontSize: 36 }}>I</div></td><td><div class="cell miss" style={{ height: 44, width: 44, fontSize: 36 }}>N</div></td></tr>
        </tbody></table>
        <p>In the above feedback,</p>
        <p>The Green higlight indicate - the character <strong>I</strong> present in the word and it is in the same position&nbsp;&nbsp;</p>
        <p>The Yellow highilgiht indicate - the character <strong>A</strong> present in the word, but in a different place</p>
        <p>The Gray highilgiht indicate - the characters are not present in the word</p>

        <div className='close' onClick={this.props.onCloseRequest}>CLOSE</div>
      </div>
    )
  }
}


